@media (orientation: landscape) {
    .Item, .ItemEmpty {
        height: 20vh;
        width: 20vh;
    }
}

@media (orientation: portrait) {
    .Item, .ItemEmpty {
        height: 20vw;
        width: 20vw;
    }
}

.Item {
    background-color: white;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    font-size: 3em;
    font-weight: 700;
    border: 2px solid black;
    box-sizing: border-box;
    user-select: none;
}

.Item:hover, .Item:active, .Item:focus {
    background-color: rgb(200, 200, 200);
    cursor: grab;
}

.ItemEmpty {
    background-color: rgba(255, 255, 255, 0.5);
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    font-size: 3em;
    font-weight: 700;
    border: 2px solid black;
    box-sizing: border-box;
    user-select: none;
}

.ItemEmpty:hover, .ItemEmpty:active {
    border: none;
}